// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import Vue from 'vue';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import routers from './routers'; /////
require('vue2-animate/dist/vue2-animate.css') /////
import store from './store'
import Global from './components/function/Global.vue';

require('../static/css/layer.css')
require('../static/js/layer.js')
//  "VueRouter"
/* eslint-disable no-new */
Vue.use(VueRouter)
Vue.use(VueResource)

const router = new VueRouter({
	mode: 'hash',
	routes: routers,
	base: __dirname,
	linkActiveClass: 'active'
})

router.beforeEach((to, from, next) => {

	if(to.matched.some(record => record.meta.login)) { //metaroute
		var id = Global.Cookie.get('id');
		if(!id) { // 
			next({
				path: '/login', // 
				query: {
					redirect: to.fullPath// query()
				} 
			})
		} else {
			next()
		}
	} else {
		next() //  next()
	}

})
let app = new Vue({
	router,
	store,
}).$mount('#app');

export default app;


